.start-page{
    
    width: 40%;
    height: 60%;
    background: #fff;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    border-radius: 6px;
    box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);

    .header{
        width: 100%;
        height: 70%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        .profile-pic{
            border: 1px solid purple;
            border-radius: 50%;
            background: #ddd;
            width:50%;
            height: 90%;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;

            img{
                height: 70%;
            }
        }
    }
    .footer{
        width: 100%;
        height: 30%; 
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
}